import React from 'react'
import { Input, Divider, Radio, Select, Button,Badge } from 'antd'
import { circleStatus } from 'components/StateLess'
const { Option } = Select;

/* -step == 0 1 modal  */
export function setDeviceTypeForms({
  current, status, currentMain, selectList
}) {
  let columns = [];
  if (current === '2') {
    columns = [
      {
        key: 'modelName',
        label: '',
        value: <Input placeholder='' style={{ width: '330px' }} />,
        getFieldDecorator: {
          initialValue: status === 'edit' ? currentMain.modelName : undefined,
          rules: [
            {
              required: true,
              message: ''
            }
          ]

        }
      },
      {
        key: 'typeId',
        label: '',
        value: (
          <Select
            optionFilterProp='children'
            showSearch allowClear
            style={{ width: 330, }}
            placeholder=''
          >
            {
              selectList && selectList.map(item => {
                return <Option key={item.id} value={item.id}>
                  {item.id}_{item.typeName}
                </Option>
              })
            }
          </Select>
        ),
        getFieldDecorator: {
          initialValue: status === 'edit' ? currentMain.typeId : undefined,
          rules: [
            {
              required: true,
              message: ''
            }
          ]
        }
      },
      {
        key: 'modelStatus',
        label: '',
        value: (
          <Radio.Group>
            <Radio value={1}></Radio>
            <Radio value={0}></Radio>
          </Radio.Group>
        ),
        getFieldDecorator: {
          initialValue: status === 'edit' ? currentMain.modelStatus : 1,
        }
      },

    ]
  } else {
    columns = [
      {
        key: 'typeName',
        label: '',
        value: <Input placeholder='' style={{ width: '330px' }} />,
        getFieldDecorator: {
          initialValue: status === 'edit' ? currentMain.typeName : undefined,
          rules: [
            {
              required: true,
              message: ''
            }
          ]

        }
      },
      {
        key: 'typeStatus',
        label: '',
        value: (
          <Radio.Group>
            <Radio value={1}></Radio>
            <Radio value={0}></Radio>
          </Radio.Group>
        ),
        getFieldDecorator: {
          initialValue: status === 'edit' ? currentMain.typeStatus : undefined,
        }
      },
      {
        key: 'unit',
        label: '',
        value: (
          <Input style={{ width: 330, }}
            placeholder='' />
        ),
        getFieldDecorator: {
          initialValue: status === 'edit' ? currentMain.unit : undefined,
        }
      },
    ]
  }
  return columns;
}
/* -step == 0 1 table  */
export function setDeviceTypeColumns(
  { current, handleEdit, handleStatus, handleFile }
) {
  let columns = []
  if (current === '1') {
    columns = [
      {
        title: '',
        dataIndex: 'typeName',
        key: 'typeName',
      }, {
        title: '',
        dataIndex: 'typeStatus',
        key: 'typeStatus',
        render: value => (
          <div>
         {value?<Badge status="success" text="" />:
          <Badge status="error" text="" />}
          </div>
        )
      }
    ]
  } else {
    columns = [
      {
        title: '',
        dataIndex: 'modelName',
        key: 'modelName',
      }, {
        title: '',
        dataIndex: 'typeName',
        key: 'typeName',
        render: value => value || '-'
      }, {
        title: '',
        dataIndex: 'modelStatus',
        key: 'modelStatus',
        render: value => circleStatus(value, 'device')
      }

    ]
  }
  return columns.concat([
    {
      title: '',
      dataIndex: 'id',
      key: 'operate', align: 'right',
      render: (value, record) => {
        let status = current === '1' ? record.typeStatus : record.modelStatus;
        return <span style={{ color: '#6236FF', cursor: 'pointer' }}>
          {current === '2' && <React.Fragment>
            <span onClick={() => handleFile(record)}></span>
            <Divider type='vertical' />
          </React.Fragment>}
          <span onClick={() => handleEdit(record)}></span>
          <Divider type='vertical' />
          <span onClick={() => handleStatus(record)}>{
            status !== 1 ? '' : ''
          }</span>
        </span>
      }
    }
  ]);
}

export function setDeviceFileColumns({
  handleFileExport, handleFileDel
}) {
  return [
    {
      title: '',
      dataIndex: 'innertype',
      key: 'innertype',
      render: value => {
        let current = setFileType().filter(item => item.id === value)[0]
        return current.typeName
      }
    }, {
      title: '',
      dataIndex: 'filename',
      key: 'filename',
    },
    {
      title: '',
      dataIndex: 'id',
      key: 'id',
      align: 'right',
      render: (value, record) => <span style={{ color: '#6236FF', cursor: 'pointer' }}>
        <span onClick={() => handleFileExport(record)}></span>
        <Divider type='vertical' />
        <span onClick={() => handleFileDel({ type: 'single', value })}></span>
      </span>
    }
  ]
}

export function setFileType() {
  return [
    {
      id: 1, typeName: ''
    },
    {
      id: 2, typeName: ''
    },
    {
      id: 3, typeName: ''
    },
  ]
}

/*  */
export function setDevListColumns({
  onEdit, onDelete, onCodeQuery
}) {
  return [
    { title: '', dataIndex: 'deviceName', key: 'deviceName', render: (value) => value || '-' },
    {
      title: '', dataIndex: 'deviceCode', key: 'deviceCode',
      render: (value) => value || '-'
    },
    {
      title: '', dataIndex: 'modelName', key: 'modelName',
      render: (value) => value || '-'
    },
    {
      title: '', dataIndex: 'guaranteePeriod', key: 'guaranteePeriod',
      render: (value) => <span>{value || '-'}</span>
    },
    {
      title: '', dataIndex: 'unit', key: 'unit',
      render: (value) => value || '-'
    },
    {
      title: '', dataIndex: 'deviceTime', key: 'deviceTime',
      render: (value) => value || '-'
    },
    {
      title: '', dataIndex: 'id', key: 'id', align: 'right',
      render: (value, record) => <span style={{ color: '#6236FF', cursor: 'pointer' }}>
        <span onClick={() => onEdit('query', value)}></span>
        <Divider type="vertical" />
        <span onClick={() => onEdit('edit', value)}></span>
        <Divider type="vertical" />
        <span onClick={() => onDelete(value)}>
          
        </span>
        <Divider type="vertical" />
        <span onClick={() => onCodeQuery(value)}>
          
        </span>
      </span>
    },
  ];
}

/*  */
export function setDevConfigColumns({
  onEdit, onDelete, queryType
}) {
  let columns = [
    { title: '', dataIndex: 'deviceName', key: 'deviceName', render: (value) => value || '-' },
    {
      title: '', dataIndex: 'deviceCode', key: 'deviceCode',
      render: (value) => value || '-'
    },
    {
      title: '', dataIndex: 'unit', key: 'unit',
      render: (value) => value || '-'
    }];
  if (queryType === 2) {
    columns = columns.concat([
      {
        title: '', dataIndex: 'customerName', key: 'customerName',
        render: (value) => value || '-'
      },
      {
        title: '', dataIndex: 'address', key: 'address',
        render: (value) => value || '-'
      },
      {
        title: '', dataIndex: 'productLine', key: 'productLine',
        render: (value) => value || '-'
      },
      {
        title: '', dataIndex: 'productSite', key: 'productSite',
        render: (value) => value || '-'
      },
    ])
  } else {
    columns = columns.concat([
      {
        title: '', dataIndex: 'guaranteePeriod', key: 'guaranteePeriod',
        render: (value) => <span>{value || '-'}</span>
      },
      // {
      //   title: '', dataIndex: 'unit', key: 'unit',
      //   render: (value) => value || '-'
      // },
      {
        title: '', dataIndex: 'createTime', key: 'createTime',
        render: (value) => value || '-'
      },
    ]);
  }
  columns = columns.concat([{
    title: '', dataIndex: 'id', key: 'id', align: 'right',
    render: (value) => <span className='order_operate_list'>
      <a onClick={() => onEdit(value, 'query')} ></a>
      <Divider type="vertical" />
      <a onClick={() => onEdit(value, 'edit')}></a>
      <Divider type="vertical" />
      <a onClick={() => onDelete(value, 'single')}></a>
    </span>
  },
  ]);
  return columns;
}

